<template>
    <div class="container-fluid">
      <div class="row no-gutter">
        <!-- The image half -->
        <div class="col-md-1 d-none d-md-flex"></div>

        <!-- The content half -->
        <div class="col-md-10 bg-light">
          <div class="login d-flex py-1">
            <!-- Demo content-->
            <div class="container">
              <div class="row">
                <div class="col-lg-12 mx-auto">
                  <h3 class="display-4">Cliente</h3>
                  <p class="text-muted mb-4">Captura de datos</p>
                  <form @submit.prevent="agregarCliente()">
                        <div class="row">
                              <b-alert
                                    :show="dismissCountDown"
                                    dismissible
                                    :variant="mensaje.color"
                                    @dismissed="dismissCountDown = 0"
                                    @dismiss-count-down="countDownChanged"
                              >
                                    {{ mensaje.texto }}
                              </b-alert>
                              <div class="col-md-6 bg-light">
                                    <float-label class="form-group mt-4 input-group">
                                    <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4" 
                                                placeholder="Nombre(s) *" 
                                                required=""
                                                autofocus=""
                                                maxlength="60"
                                                v-model="cliente.nombre">
                                    </float-label>
                                    <float-label class="form-group mt-4">
                                    <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4" 
                                                placeholder="Primer Apellido *" 
                                                required=""
                                                autofocus=""
                                                maxlength="60"
                                                v-model="cliente.primerApellido">
                                    </float-label>
                                    <float-label class="form-group mt-4">
                                          <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4" 
                                                placeholder="Segundo Apellido" 
                                                autofocus=""
                                                maxlength="60"
                                                v-model="cliente.segundoApellido">
                                    </float-label>
                                    <float-label class="form-group mt-4">
                                          <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4" 
                                                placeholder="Calle *" 
                                                required=""
                                                autofocus=""
                                                maxlength="200"
                                                v-model="cliente.calle">
                                    </float-label>
                                    <float-label class="form-group mt-4">
                                          <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4 mt-3" 
                                                placeholder="Número *" 
                                                required=""
                                                autofocus=""
                                                maxlength="15"
                                                v-model="cliente.numero">
                                    </float-label>
                              </div>
                              <div class="col-md-6 bg-light">
                                    <float-label class="form-group mt-4">
                                          <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4 mt-3" 
                                                placeholder="Colonia *" 
                                                required=""
                                                autofocus=""
                                                maxlength="150"
                                                v-model="cliente.colonia">
                                    </float-label>
                                    <float-label class="form-group mt-4">
                                          <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4 mt-3" 
                                                placeholder="Código Postal *" 
                                                required=""
                                                autofocus=""
                                                maxlength="10"
                                                v-model="cliente.codigoPostal">
                                    </float-label>
                                    <float-label class="form-group mt-4">
                                          <input type="tel" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4 mt-3" 
                                                placeholder="Teléfono *" 
                                                required=""
                                                autofocus=""
                                                maxlength="20"
                                                v-model="cliente.telefono">
                                    </float-label>
                                    <float-label class="form-group mt-4 field">
                                          <input type="text" 
                                                class="form-control rounded-pill border-0 shadow-sm px-4 mt-3 field" 
                                                placeholder="RFC *" 
                                                required=""
                                                autofocus=""
                                                maxlength="13"
                                                v-model="cliente.rfc">
                                    </float-label>
                                    <div class="form-group mb-3">
                                          <label for="exampleFormControlFile1">Agregar Documento</label>
                                          <input type="file" class="form-control-file" id="exampleFormControlFile1">
                                    </div>
                              </div>
                        </div>
                        <button type="submit" @click="accionItemMenu({ocultarInicio: true});" class="btn btn-primary btn-md text-uppercase m-4 pl-4 pr-4 rounded-pill shadow-sm float-right">Enviar</button>
                  </form>
                </div>
              </div>
            </div>
            <!-- End -->
          </div>
        </div>
        <!-- End -->
        <div class="col-md-1 d-none d-md-flex"></div>
      </div>
    </div>
  
</template>

<script>
import router from '../router/index'
import { mapState, mapActions } from "vuex";

export default {
      data() {
            return {
                  cliente: {
                        nombre: "",
                        primerApellido: "",
                        segundoApellido: "",
                        calle: "",
                        numero: "",
                        colonia: "",
                        codigoPostal: "",
                        telefono: "",
                        rfc: ""
                  },
                  dismissSecs: 5,
                  dismissCountDown: 0,
                  mensaje: { color: "", texto: "" },
                  clientes: [],
            }
      },
      computed: {
      },
      methods: {
            ...mapActions(["accionItemMenu"]),
            countDownChanged(dismissCountDown) {
                  this.dismissCountDown = dismissCountDown;
            },
            fnShowAlert(objParams) {
                  this.dismissCountDown = this.dismissSecs;
                  this.mensaje.color = objParams.color;
                  this.mensaje.texto = objParams.texto;
                  this.showAlert();
                  
            },
            agregarCliente() {
                  this.axios.post("/nuevo-cliente", this.cliente)
                        .then((res) => {
                              this.clientes.push(res.data);
                              this.ocultarInicio = true;
                              this.limpiar();
                              router.push('/');
                        })
                        .catch((e) => {
                              let msjError = "Ocurrió un error interno de servidor";
                              if (e.response.data.error.errors.nombre.message)
                                    this.mensaje.texto = e.response.data.error.errors.nombre.message;
                              this.fnShowAlert({color: "danger", texto: msjError});
                        });
            },
            limpiar() {
                  this.cliente.nombre = "";
                  this.cliente.primerApellido = "";
                  this.cliente.segundoApellido = "";
                  this.cliente.segundoApellido = "";
                  this.cliente.calle = "";
                  this.cliente.numero = "";
                  this.cliente.colonia = "";
                  this.cliente.codigoPostal = "";
                  this.cliente.telefono = "";
                  this.cliente.rfc = "";
            },
      },
}
</script>
